import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dropdown',
  templateUrl: './dropdown.component.html',
  styleUrls: ['./dropdown.component.css']
})
export class DropdownComponent implements OnInit {
  data: any = [];
  index: number;
  entities: any;
  toggle = true;
  searchText: string;
  @Input() option: any;
  @Output() messageEvent = new EventEmitter<string[]>();

  constructor() {
  }

  ngOnInit() {
    this.option.array.forEach((v: any) => {
      if (v.checked) {
        this.data.push(v);
      }
    });
  }

  getData(event: any, item: any): void {
    if (event.target.checked) {
      this.data.push(item);
    } else {
      if (this.data) {
        this.index = this.data.findIndex((x: any) => {
          return Object.keys(item).every(function (key) {
            return x[key] === item[key];
          });
        });
        this.data.splice(this.index, 1);
      }
    }
    this.messageEvent.emit(this.data);
  }

  selectAll(getArr: any): void {
    getArr.forEach((element: any) => {
      if (element.checked === true) {
        return false;
      } else {
        element.checked = true;
        this.data.push(element);
      }
    });
    this.messageEvent.emit(this.data);
  }

  unSelectAll(arrData: any): void {
    arrData.forEach((element: any) => {
      if (element.checked === false) {
        return true;
      } else {
        element.checked = false;
        this.index = this.data.findIndex((x: any) => {
          return Object.keys(element).every(function (key) {
            return x[key] === element[key];
          });
        });
        this.data.splice(this.index, 1);
      }
    });
    this.messageEvent.emit(this.data);
  }

  updateSelection(position: number, array: any, event: any, item: any): void {
    this.data = [];
    array.forEach((x: any, index: number) => {
      if (position !== index) {
        x.checked = false;
      }
    });
    if (event.target.checked) {
      this.data.push(item);
    }
    this.messageEvent.emit(this.data);
  }

  clear() {
    this.searchText = '';
  }

  delete(item: any) {
    this.index = this.data.findIndex((x: any) => {
      return Object.keys(item).every(function (key) {
        return x[key] === item[key];
      });
    });
    this.data.splice(this.index, 1);
    const check = this.option.array.forEach((element: any) => {
       Object.keys(item).every(function (key): any {
       if ( element[key] === item[key]) {
          return element.checked = false;
       }
      });
    });
    this.toggle = true;
  }
}
